<template>
  <div :color="color" class="nav-link">
    <router-link :to="url">{{ name }}</router-link>
    <div class="underline">
      <svg class="underline-svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 347.98 33.7">
          <path :id="i" class="line-path" d="M6.27,28.26C24.58,8.44,36.77,2.87,45.07,4.39,58.65,6.88,58.92,27.75,70.94,28.77c13.77,1.17,24.3-25.34,36.7-23.87,11,1.31,10.77,23.32,21.32,24.38C140.71,30.46,150.13,4.09,163.22,5c11.88.82,14.07,23.28,25.16,23.71,11.65.45,18-24,30.06-23.71,11.69.3,15,23.33,26.92,23.71,12.31.39,18.55-24,31.1-23.71,11.62.3,13.62,21.36,28,23.71C312.58,30,324.66,25.29,343.18,5" />
      </svg>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import ColorsMixin from '../mixins/ColorsMixin.js'

export default {
  name: 'NavLink',
  props: [
    'url',
    'name',
    'color',
    'i'
  ],
  mixins: [
    ColorsMixin
  ],
  mounted: function() {
    var vm = this;
    $("#" + this.i).css('stroke', this.color);
    $('.nav-link').hover(
      function() {
        var id = $(this).find('.line-path').attr('id');
        var color = $(this).attr('color');
        vm.animateNavLink(id);
        vm.animateProjectsButton(id);
        vm.animateButton(color, id);
        vm.recolor(color, id);
      }, function() {
        var id = $(this).find('.line-path').attr('id');
        vm.removeAnimateNavLink(id);
        vm.removeAnimateButton('black', id);
        vm.recolor('black', id);
        vm.removeAnimateProjectsButton(id);
      }
    )
  }

}

</script>

<style>
.line-path {
    fill: none;
    stroke: black;
    stroke-miterlimit: 10;
    stroke-width: 10px;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

.animate-nav-link {
  animation: dash 1s linear forwards;
}

.nav-link:hover > .underline > .underline-svg >  .line-path {

}

.nav-link a {
  font-weight: 600;
  text-transform: uppercase;
}

.nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 20px;
}

.underline-svg {
  width: 80px;
  position: relative;
  top: -10px;
}
</style>
