import $ from 'jquery'

export default {
  methods: {
    recolor(color) {
      $('.large').css('outline-color', color);
      $('.small').css('border-color', color);
      $('.logo .text').css('color', color);
      $('.social-icons > a > svg').css('fill', color);
      if (color === "black") {
        $('.large').css('background-color', 'white');
      } else {
        $('.large').css('background-color', color);
      }
    },
    animateNavLink(pageId) {
      $("#" + pageId).addClass('animate-nav-link');
    },
    removeAnimateNavLink(pageId) {
      $("#" + pageId).removeClass('animate-nav-link');
    },
    animateButton(color, pageId) {
      $('#' + pageId + '.btn-wrapper').find('.overlay').addClass('animate-btn-overlay')
      $('#' + pageId + '.btn-wrapper').find('.overlay').css('background-color', color);
      $('#' + pageId + '.btn-wrapper').css('border-color', color);
      $('#' + pageId + '.btn-wrapper').find('a').css('color', 'white');
    },
    removeAnimateButton(color, pageId) {
      $('#' + pageId + '.btn-wrapper').find('.overlay').removeClass('animate-btn-overlay')
      $('#' + pageId + '.btn-wrapper').find('.overlay').css('background-color', 'black');
      $('#' + pageId + '.btn-wrapper').css('border-color', 'black');
      $('#' + pageId + '.btn-wrapper').find('a').css('color', 'black');
    },
    animateProjectsButton(pageId) {
      if (pageId === "2") {
        $('.right > a > svg').addClass('animate-arrow');
        $('.right > a > .text').addClass('animate-text');
      }
    },
    removeAnimateProjectsButton(pageId) {
      if (pageId === "2") {
        $('.right > a > svg').removeClass('animate-arrow');
        $('.right > a > .text').removeClass('animate-text');
      }
    }
  }
}
