<template>
  <div :id="pageId" class="btn-wrapper">
    <router-link :to="url">{{ name }}</router-link>
    <div class="overlay"></div>
  </div>
</template>

<script>
import ColorsMixin from '../mixins/ColorsMixin.js'
import $ from 'jquery'

export default {
  name: 'CustomButton',
  mixins: [
    ColorsMixin
  ],
  props: [
    'url',
    'name',
    'color',
    'pageId'
  ],
  mounted() {
    var vm = this;
    $('.btn-wrapper').hover(
      function() {
        vm.recolor(vm.color);
        vm.animateButton(vm.color, vm.pageId);
        vm.animateNavLink(vm.pageId)
      }, function() {
        vm.recolor('black');
        vm.removeAnimateButton(vm.color, vm.pageId);
        vm.removeAnimateNavLink(vm.pageId)
      }
    )
  }
}

</script>

<style>
.btn-wrapper {
  position: relative;
  padding: 10px;
  border: 2px solid black;
  transition: border-color .3s ease;
}

.btn-wrapper > a {
  position: relative;
  z-index: 2;
  transition: color .3s ease;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.2em;
}

.overlay {
  position: absolute;
  left: -5px;
  right: -5px;
  top: -5px;
  bottom: -5px;
  background-color: black;
  clip-path: inset(0 100% 0 0);
  transition: clip-path .5s ease, background-color .3s ease;
  cursor: pointer;
}

.animate-btn-overlay {
  clip-path: inset(0 0 0 0);
}

.btn-wrapper:hover > .overlay {
  clip-path: inset(0 0 0 0);
}

.btn-wrapper:hover > a {
  color: white;
}



</style>
