<script>
  import Logo from '@/components/Logo.vue'
  import NavLink from '@/components/NavLink.vue'

  export default {
    name: 'Header',
    data: function() {
      return {
        menuOpen: false
      }
    },
    components: {
      Logo,
      NavLink
    },
    methods: {
      handleMenu: function () {
        if (!this.menuOpen) {
          this.menuOpen = true
        } else {
          this.menuOpen = false
        }
      }
    },
    watch: {
      $route() {
        this.menuOpen = false
      }
    }
